*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


nav.navbar { 
  display: flex;
  padding: 20px;
  background: #181717; 
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
}

.navbar-logged {
  display: inline;
  justify-content: flex-end;

}

button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  border-radius: 10px;
  margin: 0 10px; 
}

button.navbar-button a {
  text-decoration: none;
}

.home-btn {
  display: flex;
  width: 30px;
  height: 30px;
  
}


/*------------Home----------------*/

.parking {
  background-image: url(https://res.cloudinary.com/dtsag4ss2/image/upload/v1608032953/Fotos/nikhil-thakar-s2_dalocm.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100vh;
}

.parking h1  {
  display: flex;
  justify-content: center;
  font-size: 30px;
  padding: 30px;
  color: rgb(195, 194, 194);
  margin-top: 50px;
}
.parking h2  {
  display: flex;
  justify-content: center;
  font-size: 15px;
  margin-top: 30px;
  color: rgb(195, 194, 194);
}

/*--------------Login  &  Signup---------------*/
.login {
  background-image: url(https://res.cloudinary.com/dtsag4ss2/image/upload/v1607961079/Fotos/wp4429934_pepgvu.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 90.3vh;
  overflow: hidden;
}

.cuenta {
  color: #f2f2f2;

}
.cuenta p{
  text-decoration: none;
  padding: 10px;
}

.login h1 {
  display: flex;
  justify-content: center;
  font-size: 50px;
  color: rgb(195, 194, 194);
}

.login-button {
  margin: 10px;
  padding: 5px;
  border: 10px;
  width: 100px;
  border-radius: 3rem;
  border: 1px solid black;
  background-color: rgb(233, 233, 233);
  text-decoration: none;
}
.login-form h3 {
  text-align: center;
  color: black;
}
.login-form {
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 10%;
  margin: 5% auto;
  text-align: center;
}
.login input[type="text"],
.login input[type="password"] {
  max-width: 400px;
  width: 80%;
  line-height: 3em;
  font-family: "Ubuntu", sans-serif;
  margin: 1em 2em;
  border-radius: 3rem;
  border: 2px solid #f2f2f2;
  outline: none;
  padding-left: 10px;
}
.login-form input[type="button"] {
  height: 30px;
  width: 100px;
  background: #fff;
  border: 1px solid #f2f2f2;
  border-radius: 20px;
  color: slategrey;
  text-transform: uppercase;
  font-family: "Ubuntu", sans-serif;
  cursor: pointer;
}

.sign-up {
  color: #f2f2f2;
  margin: 10px 0;
  cursor: pointer;
}


 /*---------------------profile-----------------*/

 .profileBackground{
  background-image: url(https://res.cloudinary.com/dtsag4ss2/image/upload/v1608032688/Fotos/boJykZ_omnn2o.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 100vh;
 }

 .titleProfile {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 20px;
  color: white;
  margin-top: 70px;
  margin-bottom: 250px;
  padding: 20px;
 }

 .botonesProfile {
   display: flex;
   flex-direction: row;
   justify-content: space-around;
 }

 .profileButton {
  width: 100px;
  background-color: rgb(103, 103, 126);
  color: black;
  padding: 10px 20px;
  border-radius: 10px;
  -webkit-box-shadow: 7px 7px 5px -1px rgba(219,212,219,1);
  -moz-box-shadow: 7px 7px 5px -1px rgba(219,212,219,1);
  box-shadow: 7px 7px 5px -1px rgba(219,212,219,1);
 }
 .profileButton a {
   display: inline;
   text-decoration: none;
   
   color: #fff;
 }



 /*-----------------Edit Profile--------------*/

 .editProfileBackground{
  background-image: url(https://res.cloudinary.com/dtsag4ss2/image/upload/v1607961079/Fotos/wp4429934_pepgvu.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 100vh;
 }



 .edit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 40px;
  flex-wrap: nowrap;
}

