  font-size: 20px;
  margin-top: 30px;
  line-height: 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.edit h1 {
  color: rgb(195, 194, 194);
  margin-top: 75px;
}

.edit-info label {
  color: rgb(195, 194, 194);
  font-weight: bold;
}
.edit-info input[type="text"] {
  max-width: 400px;
  width: 80%;
  line-height: 25px;
  border: 2px solid #51616e;
  padding-left: 10px;
}

.edit-info textarea[type="text"] {
  max-width: 400px;
  width: 80%;
  line-height: 25px;
  border: 2px solid #51616e;
  padding-left: 10px;
}

.profile-image {
  width: 40%;
  height: 40%;
  margin: 10px;
  border-radius: 5%;
}

