 @import 'https://fonts.googleapis.com/css?family=Montserrat:300,400,700';
 *, *:before, *:after {
	 box-sizing: border-box;
}
 .rwd-table {
	 margin: 1em 0;
	 min-width: 300px;
}
 .rwd-table tr {
	 border-top: 1px solid #ddd;
	 border-bottom: 1px solid #ddd;
}
 .rwd-table th {
	 display: none;
}
 .rwd-table td {
	 display: block;
}
 .rwd-table td:first-child {
	 padding-top: 0.5em;
}
 .rwd-table td:last-child {
	 padding-bottom: 0.5em;
}
 .rwd-table td:before {
	 content: attr(data-th) ": ";
	 font-weight: bold;
	 width: 6.5em;
	 display: inline-block;
}

.align{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.cardDetail img {
  width: 80%;

  border-radius: 15%;
  -webkit-box-shadow: 15px 15px 15px -13px rgba(0,0,0,0.41);
  -moz-box-shadow: 15px 15px 15px -13px rgba(0,0,0,0.41);
  box-shadow: 15px 15px 15px -13px rgba(0,0,0,0.41);
  margin-left: 40px;
 }

 .carDetailsButton {
  width: 100px;
  background-color: whitesmoke;
  color: black;
  padding: 10px 20px;
  margin: 15px;
  border-radius: 10px;
  -webkit-box-shadow: 7px 7px 15px 2px rgba(0,0,0,0.75);
  -moz-box-shadow: 7px 7px 15px 2px rgba(0,0,0,0.75);
  box-shadow: 7px 7px 15px 2px rgba(0,0,0,0.75);
 }
 .carDetailsButton a {
   display: inline;
   text-decoration: none;
   
   color: black;
 }


 @media (min-width: 480px) {
	 .rwd-table td:before {
		 display: none;
	}
}
 .rwd-table th, .rwd-table td {
	 text-align: left;
}

.align{
  display: flex;
  align-items: center;
  flex-direction: column;
